import React, { ReactNode } from 'react';
import { Container, Box } from '@mui/material';
import { useSpring, animated } from 'react-spring';
import Header from 'components/Header';
import Footer from 'components/Footer';
import SEO from 'components/Utils/SEO';

interface Props {
  children: ReactNode;
  pageName?: string;
  showTitle?: boolean;
  description?: ReactNode;
  hideFooter?: boolean;
}
const Page = ({ children, pageName, showTitle = false, description, hideFooter = false }: Props) => {
  const titleAnimation = useSpring({
    from: { opacity: 0, transform: 'translateX(-30px)' },
    to: { opacity: 1, transform: 'translateX(0)' },
  });
  const descriptionAnimation = useSpring({
    from: { opacity: 0, transform: 'translateX(30px)' },
    to: { opacity: 1, transform: 'translateX(0)' },
  });

  return (
    <div data-layout="page">
      <SEO title={pageName} />
      <Header />
      <div>
        {showTitle ? (
          <Container sx={{ marginTop: '5rem', marginBottom: '3rem' }}>
            <Box component={animated.h1} style={titleAnimation} sx={{ color: 'text.primary' }}>
              {pageName}
            </Box>
            {description && (
              <Box
                component={animated.p}
                style={descriptionAnimation}
                sx={{ color: 'text.secondary', whiteSpace: 'pre-line' }}
              >
                {description}
              </Box>
            )}
          </Container>
        ) : null}
        <Box sx={{ backgroundColor: 'common.white' }}>{children}</Box>
      </div>
      {!hideFooter && <Footer />}
    </div>
  );
};

export default Page;
